import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';

import Demo1 from './demo1';

class Page extends PureComponent {
    displayName = 'Page';
    render () {
        return (
            <div className='main-container'>
                <h1 className='page-h1'>SubmitButton </h1>
                <blockquote className='page-tip'>
                     loading  
                </blockquote>

                <h1 className='page-h1'></h1>

                <Label grid={0.5} className='code-col'>
                    <Label className='code-box'>
                        <Demo1></Demo1>
                    </Label>
                </Label>

                <h2 className='page-h2'>API</h2>
                <h3 className='page-h3'>SubmitButton</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'successTip', desc: '', type: 'string', default: ''},
                    {param: 'errorTip', desc: '', type: 'string', default: ''},
                    {param: 'data', desc: '', type: 'object', default: ''},
                    {param: 'back', desc: '', type: 'bool', default: ''},
                    {param: 'isValid', desc: '', type: 'function', default: ''},
                    {param: 'onSubmit', desc: '', type: 'function', default: ''},
                    {param: 'Button', desc: '-', type: '-', default: ''}
                ]}></Table>
            </div>
        );
    }
}

export default Page;



